<template>
    <el-container>
        <left-aside/>
        <el-container>
            <!--<el-header></el-header>-->
            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="5">
                        <div style="font-size: 20px;height: 37px">
                            iTalkList
                        </div>
                    </el-col>
                    <el-col :span="10">
                        <el-input placeholder="搜索标题、摘要、字幕，支持中英文关键词" suffix-icon="el-icon-search"/>
                    </el-col>
                </el-row>

                <filter-list listType="talk" :filter-list="filterList"/>

                <el-table
                        ref="multipleTable"
                        :data="tableData3"
                        tooltip-effect="dark"
                        style="width: 100%"
                        @selection-change="">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            label="标题">
                        <template slot-scope="scope">{{ scope.row.date }}</template>
                    </el-table-column>
                    <el-table-column
                            prop="name"
                            label="ID">
                    </el-table-column>
                    <el-table-column
                            prop="address"
                            label="难度"
                            show-overflow-tooltip>
                    </el-table-column>
                    <el-table-column
                            prop="address"
                            label="状态"
                            show-overflow-tooltip>
                    </el-table-column>

                    <el-table-column
                            label="操作">
                        <template slot-scope="scope">
                            <el-button type="text" size="small">编辑</el-button>
                            <el-dropdown class="dropdown-edit" trigger="click" @command="handleCommand">
                              <span class="el-dropdown-link">
                                发布<i class="el-icon-arrow-down el-icon--right"></i>
                              </span>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item :command="{key:'release',value:''}">发布</el-dropdown-item>
                                    <el-dropdown-item :command="{key:'time',value:''}">定时发布</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </template>
                    </el-table-column>
                </el-table>
            </el-main>
            <el-footer style="text-align: center">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage1"
                        :page-size="10"
                        layout="total, prev, pager, next"
                        :total="100">
                </el-pagination>
            </el-footer>

            <release-timing :isShow="isShowSetTime" @hideTiming="isShowSetTime = false"/>
        </el-container>
    </el-container>
</template>

<script>
    import FilterList from '../components/FilterListItem'
    import ReleaseTiming from '../components/ReleaseTimingItem'
    import LeftAside from '../components/LeftAsideItem'

    export default {
        components: {
            FilterList,
            ReleaseTiming,
            LeftAside
        },
        data() {
            return {
                tableData3: [{
                    date: '2016-05-03',
                    name: '王小虎',
                    address: '上海市普陀区金沙江路 1518 弄'
                }],
                multipleSelection: [],
                currentPage1: 5,
                isShowSetTime: false,
                filterList:{},
            }
        },
        methods: {
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            },
            jumpPage(key) {
                console.log(key)
                this.$router.push(key)
            },
            handleCommand(command) {
                console.log(command)
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                }
            },
        },
    }
</script>

<style scoped>
    .el-dropdown-link {
        cursor: pointer;
        color: #409eff;
        font-size: 12px
    }

    .dropdown-edit {
        margin-left: 5px
    }
</style>